<template>
  <div class="content">
   <div class="operation">
        <el-form :inline="true" label-width="100px" label-position="right">
          <el-form-item label="所属店铺"  class="mgb0">
            <el-select
              v-model="tempData.shopId"
              v-loading="loading2"
              filterable
              remote
              placeholder="输入店铺分类模糊查询"
              @focus="searchShop('')"
              :remote-method="searchShop"
              clearable
            >
              <el-option
                v-for="item in shopList"
                :key="item.id"
                :label="item.shopName"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="下单时间" class="mgb0">
            <el-date-picker
              type="date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              v-model="tempData.startTime"
            ></el-date-picker>
            <span>至</span>
            <el-date-picker
              type="date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              v-model="tempData.endTime"
            ></el-date-picker>
          </el-form-item>
          <el-form-item  class="mgb0">
            <el-button type="primary" @click="searchList" icon="el-icon-search">查询</el-button>
          </el-form-item>
        </el-form>
    </div>
    <div class="table">
        <section class="chartCon" v-loading="loading">
          <div id="myChart" :style="{width:'100%', height:'600px'}"></div>
        </section>
  </div>
  </div>
</template>

<script>
import pagination from "@/components/pagination";
import { statistics, order } from "@/api/index.js";
export default {
  components: {
    pagination
  },
  data() {
    return {
      shopList: [],
      loading2: false,
      //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {},
      //搜索时用的数据
      searchData: {},
      xLines: [],
      yLines: []
    };
  },
  methods: {
    initData: function(page, size) {
      this.loading = true;
      this.xLines.length = 0;
      this.yLines.length = 0;
      this.loading = true;
      this.searchData = this.$common.deepCopy(this.tempData);
      statistics.custom(this.searchData).then(res => {
        this.tableData = res.rows;
        this.tableData.forEach(item => {
          this.xLines.push(item.createTime);
          this.yLines.push(item.userCount);
        });
        this.yLines.reverse();
        this.xLines.reverse();
        this.drawLine();
        setTimeout(() => {
          this.loading = false;
        }, 500);
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    searchList: function() {
      this.searchData = this.$common.deepCopy(this.tempData);
      this.initData(1);
    },
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      myChart.setOption({
        colors: ["#5793f3", "#d14a61", "#675bba"],
        title: { text: "订单数量统计" },
        tooltip: {},
        xAxis: {
          type: "category",
          axisTick: {
            alignWithLabel: true
          },
          data: this.xLines,
          axisLine:{
                 lineStyle: {
                    color:"#5793f3"
                }
          }
        },
        grid: {
          right: "20%"
        },
        legend: {
          data: ["销量线图", "销量柱状图"]
        },
        yAxis: {
          type: "value",
          name: "订单数量",
          min: 0,
          axisLine:{
            lineStyle: {
            color:"#5793f3"
                }
          }
        },
        series: [
          {
            name: "销量线图",
            type: "line",
            data: this.yLines.reverse(),
         itemStyle:{
               normal:{
                 color:"#675bba"
               }
            }
          },
          {
            name: "销量柱状图",
            type: "bar",
            barWidth:50,
            data: this.yLines.reverse(),
            itemStyle:{
               normal:{
                 color:"#5793f3"
               }
            }
          }
        ]
      });
    },
    searchShop(shopName) {
      this.$common.throttle(() => {
        order.orderShop({ shopName }).then(res => {
          this.shopList = res.data;
        });
      }, 1000);
    }
  },
  beforeMount() {
    this.initData();
  }
};
</script>




